import React, { FunctionComponent } from 'react'
import { Spinner, SpinnerProps, Box, BoxProps } from '@chakra-ui/core'

interface Props {
  spinnerProps?: SpinnerProps
  boxProps?: BoxProps
}

const StyledSpinner: FunctionComponent<Props> = (props) => {
  return (
    <Box
      {...props.boxProps}
      justifyContent='center'
      alignItems='center'
      margin='auto'
    >
      <Spinner
        size='xl'
        speed='0.8s'
        thickness='4px'
        justifySelf='center'
        {...props.spinnerProps}
      />
    </Box>
  )
}

export default StyledSpinner
