import React, { FunctionComponent } from 'react'
import { Badge, BadgeProps } from '@chakra-ui/core'
import theme from "../gatsby-plugin-chakra-ui/theme"

const StyledBadge: FunctionComponent<BadgeProps> = (props) => {
  const { children } = props

  return (
    <Badge
      mb='1'
      mr='1'
      fontSize='0.6em'
      variant='outline'
      color={theme.colors.white}
      borderWidth='1px'
      boxShadow={`2px 3px 0px ${theme.colors.black}`}
      borderColor={theme.colors.white}
    >
      {children}
    </Badge>
  )
}

export default StyledBadge
