import React, { FunctionComponent, useEffect, useState } from "react"
import StyledDivider from "../StyledDivider"
import { IComment } from "../../models/comment"
import CommentList from "./CommentList"
import LeaveComment from "./LeaveComment"

interface CommentsProps{
    slug: string
}

const Comments: FunctionComponent<CommentsProps> = ({ slug }) => {
  const [comments, setComments] = useState<Array<IComment>>([])
  const [fetching, setFetching] = useState(false)
  const [refetch, setRefetch] = useState(true)

  // Add the new comment as the first item in the array, so that we don't have to refetch from the API.
  const handleAddComment = (comment: IComment) => setComments(comments => [...comments, comment])

  useEffect(() => {
    if (!slug || !refetch) return

    const fetchData = async () => {
      try
      {
        setFetching(true)
        const resp = await fetch(`${process.env.COMMENTS_ENDPOINT}/${slug}`)
        const data = await resp.json()
        setComments(data || [])
        setFetching(false)
        setRefetch(false)
      }
      catch(error)
      {
        setFetching(false)
        throw error
      }
    }

    fetchData()
  }, [slug, refetch])

  return(
    <>
      <CommentList fetching={fetching} comments={comments} />
      <StyledDivider />
      <LeaveComment onAddComment={handleAddComment} slug={slug} />
    </>
  )
}

export default Comments
