import React from "react"
import { Link, graphql } from "gatsby"
import { Box, Heading, Text } from "@chakra-ui/core"
import Bio from "../components/bio"
import Layout from "../components/layout"
import SEO from "../components/seo"
import theme from "../gatsby-plugin-chakra-ui/theme"
import Tag from "../components/Tag"
import Subscribe from "../components/Subscribe"
import Tweet from "../components/Tweet"
import StyledDivider from "../components/StyledDivider"
import Comments from "../components/Comments/index"
import { MDXRenderer } from "gatsby-plugin-mdx"
import { MDXProvider } from "@mdx-js/react"

const BlogPostTemplate = ({ data, pageContext, ...rest }) => {
  const post = data.mdx
  const { title: siteTitle, keywords, siteUrl } = data.site.siteMetadata
  const { previous, next } = pageContext
  const slug = post.frontmatter.title.replace(/[^0-9a-z]/gi, "")

  return (
    <Layout title={siteTitle}>
      <Box width="100%" height="100%" backgroundColor={theme.colors.black}>
      <SEO
        title={post.frontmatter.title}
        description={post.frontmatter.description || post.excerpt}
        pathname={rest.location.pathname}
        keywords={keywords}
      />
      <Bio />
      <article style={{marginLeft: "10px", marginRight: "10px", marginTop: "10px"}}>
        <header>
          <Heading as="h3" fontSize="2xl">
            {post.frontmatter.title}
          </Heading>
          <Text fontSize='xs'>{`${post.frontmatter.date} | ${post.timeToRead} minutes to read`}</Text>
          {post.frontmatter.tags && post.frontmatter.tags.length > 0 && (
            <Box display="flex" flexDirection="row" flexWrap="wrap">
              <Text fontSize='xs' mr='5px'>Tags:</Text>
              {post.frontmatter.tags.map((tag) => (
                  <Tag key={tag} name={tag}/>
              ))}
            </Box>
          )}

          <Box mb="20px" />
        </header>
        <MDXProvider components={{
          h1: (props) => <Heading as="h1" size="2xl" mt="20px" {...props} />,
          h2: (props) => <Heading as="h2" size="xl" mt="20px" {...props} />,
          h3: (props) => <Heading as="h3" size="lg" mt="20px" {...props} />,
          h4: (props) => <Heading as="h4" size="md" mt="20px" {...props} />,
          h5: (props) => <Heading as="h5" size="sm" mt="20px" {...props} />,
          h6: (props) => <Heading as="h6" size="xs" mt="20px" {...props} />,
          blockquote: (props) => <blockquote style={{marginTop: "15px", marginBottom: "15px", borderLeftColor: `${theme.colors.white}`, color: `${theme.colors.white}`}} {...props} />,
          li: (props) => <li style={{marginLeft: "20px"}} {...props} />
        }}>
          <MDXRenderer>{post.body}</MDXRenderer>
        </MDXProvider>
          <StyledDivider />
          <Tweet pathname={rest.location.pathname} mb="10px" siteUrl={siteUrl} title={post.frontmatter.title}/>
          <Subscribe />
          <StyledDivider />
          <Comments slug={slug} />
      </article>

      <nav style={{marginLeft: "10px", marginRight: "10px"}}>
        <ul
          style={{
            display: `flex`,
            flexWrap: `wrap`,
            justifyContent: `space-between`,
            listStyle: `none`,
            padding: 0,
          }}
        >
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                 {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} 
              </Link>
            )}
          </li>
        </ul>
      </nav>
      </Box>
    </Layout>
  )
}

export default BlogPostTemplate

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        keywords
        siteUrl
      }
    }
    mdx(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      body
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        tags
      }
      timeToRead
    }
  }
`
