import React, { FunctionComponent, useEffect, useState } from "react"
import { Box, Text, Heading, Flex } from "@chakra-ui/core"
import theme from "../../gatsby-plugin-chakra-ui/theme"
import AutoSizeTextarea from "../../components/AutoSizeTextarea"
import StyledSpinner from "../../components/StyledSpinner"
import { IComment } from "../../models/comment"

interface CommentListProps {
  fetching: boolean,
  comments: Array<IComment>
}

const CommentList: FunctionComponent<CommentListProps> = ({ fetching, comments }) => {
  return (
    <>
      <Heading
        as="h2"
        fontSize="3xl"
        mb="25px"
        ml="10px"
        textAlign="left"
      >
        Comments
      </Heading>
      {fetching && <StyledSpinner spinnerProps={{marginLeft: "10px"}} />}
      {comments.map(comment => {
        console.log('comment: ', comment)

        return (
          <Box
            borderWidth="1px"
            backgroundColor={theme.colors.black}
            mb="10px"
            mr="10px"
            ml="10px"
            padding="10px"
            borderColor={theme.colors.white}
            boxShadow={`2px 2px 0px ${theme.colors.white}}`}
            borderRadius="5px"
            key={comment.RowKey}
          >
            <Flex direction="row">
              <Box mr="25px">
                <Text fontWeight="extrabold">
                  {comment.Name} @ {new Date(comment.Date).toLocaleString()}
                </Text>
              </Box>
            </Flex>
            <Flex direction="column" pt="10px">
              <AutoSizeTextarea
                value={comment.Text}
                isDisabled={true}
                backgroundColor={theme.colors.black}
                color={theme.colors.white}
                borderWidth="0px"
                cursor="default"
                resize="none"
                padding="0px"
                fontSize="sm"
                _disabled={{color: theme.colors.white}}
              />
            </Flex>
          </Box>
        )
      })}
    </>
  )
}

export default CommentList
