import React, { FunctionComponent, useEffect, useState } from "react"
import {
  Box,
  Text,
  Heading,
  Flex,
  Textarea,
  Input,
  Button,
} from "@chakra-ui/core"
import theme from "../../gatsby-plugin-chakra-ui/theme"
import validateEmail from "../../lib/validateEmail"
import StyledSpinner from "./../StyledSpinner"
import { AiFillCheckCircle } from "react-icons/ai"
import { IComment } from "../../models/comment"

interface LeaveCommentProps {
  slug: string
  onAddComment: (comment: IComment) => void 
}

const LeaveComment: FunctionComponent<LeaveCommentProps> = ({ slug, onAddComment }) => {
  const [text, setText] = useState("")
  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [focused, setFocused] = useState(false)
  const [required, setRequired] = useState(false)
  const [submitting, setSubmitting] = useState(false)
  const [submitted, setSubmitted] = useState(false)

  const handleTextAreaChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setText(e.currentTarget.value)
    setRequired(true)
  }
  const invalidEmail = (): boolean => {
    return required && !validateEmail(email)
  }
  const invalidName = (): boolean => {
    return required && name.length < 3
  }
  const invalidText = (): boolean => {
    return required && text.length < 4
  }
  const submitCommentDisabled = () => {
    return !required || invalidEmail() || invalidName() || invalidText()
  }
  const resetData = () => {
    setText("")
    setEmail("")
    setName("")
    setFocused(false)
    setRequired(false)
  }
  const submitComment = () => {
    setSubmitting(true)

    fetch(process.env.COMMENTS_ENDPOINT, {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify({
        PartitionKey: slug,
        Text: text,
        Name: name,
        Email: email
      })
    })
    .then((resp: Response) => {
      resp.json()
        .then(data => {
          setSubmitting(false)
          setSubmitted(true)
          resetData()
          setTimeout(() => {
            setSubmitted(false)
          }, 3000);
          onAddComment(data)
        })
    })
    .catch((error) => {
      setSubmitting(false)
      throw error
    })
  }

  // Wakeup the Azure Function in case a comment gets submitted.
  useEffect(() => {
    if (!required) return

    const wakeup = async () => {
      const resp = await fetch(`${process.env.WAKEUP_ENDPOINT}`)
    }

    wakeup()
  }, [required])

  return (
    <Box ml="10px" mr="10px">
      <Heading as="h2" fontSize="3xl" mb="25px" textAlign="left">
        Leave a Comment
      </Heading>
      {submitting ? (
        <StyledSpinner
          spinnerProps={{ marginLeft: "10px", marginBottom: "25px" }}
        />
      ) : (
        !submitted &&
        <Flex direction="column" alignItems="flex-start">
          <Textarea
            isRequired={required}
            value={text}
            placeholder="Comment"
            _placeholder={{ color: theme.colors.white }}
            onChange={handleTextAreaChange}
            backgroundColor={theme.colors.black}
            color={theme.colors.white}
            fontSize="sm"
            focusBorderColor={theme.colors.accent}
            onFocus={() => setFocused(true)}
            isInvalid={invalidText()}
          />
          <Box mb="20px" />
          {focused && (
            <>
              <Input
                value={email}
                type="email"
                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                  setEmail(e.currentTarget.value)
                }
                placeholder="Email"
                borderColor={theme.colors.white}
                focusBorderColor={theme.colors.accent}
                isInvalid={invalidEmail()}
                fontSize="16px"
                isRequired={required}
                width="80%"
                bg={theme.colors.black}
                color={theme.colors.white}
              />
              <Box mb="20px" />
              <Input
                value={name}
                type="text"
                onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                  setName(e.currentTarget.value)
                }
                placeholder="Name"
                borderColor={theme.colors.white}
                focusBorderColor={theme.colors.accent}
                isInvalid={invalidName()}
                fontSize="16px"
                isRequired={required}
                width="80%"
                bg={theme.colors.black}
                color={theme.colors.white}
              />
              <Box mb="20px" />
              <Button
                onClick={submitComment}
                color={theme.colors.black}
                bg={theme.colors.accent}
                size="xs"
                minWidth="auto"
                height="30px"
                width="100px"
                isDisabled={submitCommentDisabled()}
                _hover={{ bg: theme.colors.accent }}
              >
                <Text fontSize="sm" color={theme.colors.black}>
                  Submit
                </Text>
              </Button>
              <Box mb="20px" />
            </>
          )}
        </Flex>
      )}
      {submitted && (
        <Flex direction="row" alignItems="center" pb="25px">
          <Box
            as={AiFillCheckCircle}
            backgroundColor={theme.colors.green[700]}
            fontSize="100px"
            borderRadius="50px"
          />
          <Box pl="15px">
            <Text>Your comment has been submitted!</Text>
          </Box>
        </Flex>
      )}
    </Box>
  )
}

export default LeaveComment
