import React, { FunctionComponent } from "react";
import { Text, Button, ButtonProps, Box, BoxProps, Link } from "@chakra-ui/core";
import { FiTwitter } from "react-icons/fi"
import theme from "../gatsby-plugin-chakra-ui/theme"

type TweetProps = {
  pathname: string
  title: string
  siteUrl: string
} & ButtonProps

const Tweet: FunctionComponent<TweetProps> = ({pathname, title, siteUrl, ...buttonProps}) => {
  const url = `https://twitter.com/intent/tweet?url=${encodeURI(siteUrl + pathname)}&text=${encodeURI("Check out this blog post: " + `"${title}"` + " by @richardrobberse")}`
  const handleClick = () => window.open(url, '_blank')

  return (
    <Button
      backgroundColor="#1d9bf0" 
      color="#ffffff" 
      height="2.3rem" 
      onClick={handleClick}
      _hover={{backgroundColor: theme.colors.tweetBg}}
      {...buttonProps}
    >
      <Box as={FiTwitter} mr="5px"/>
      <Text>Tweet</Text>
    </Button>
  );
};

export default Tweet;
